        props: {
            showDialog: {
                type: Boolean,
                default: false
            }
        },
        data: function () {
            return {
                boolean: false,
                qClient: new QClient(),
                radio1: "",
                ledTarget: `/lit3d/slave/0/video`,
                ledTargetSs: `/lit3d/slave/0/ss`,
                ledTargetSpash: `/lit3d/slave/0/splash`,
                presList: [{
                    id: "1",
                    subtitle_ru: "Авиационная логистика"
                },{
                    id: "2",
                    subtitle_ru: "Железнодорожная логистика"
                },{
                    id: "3",
